import React, { useEffect, useState } from 'react';
import './burger.css'
import { Card,Container,Grid } from '@mui/material';
const Burger = () => {

   const[burger,setBurger]=useState([])
    useEffect(()=>{
        fetch('burger.json')
        .then(res=>res.json())
        .then(data=>setBurger(data))
    },[])

    return (
        <div>
            
            <Container>
            <Grid container spacing={2}>
                {
                    burger.map(brgr=>

                    <Grid item xs={6} md={4}>
                        <Card sx={{ maxWidth: 600,backgroundColor:'black'}}>
                            <div className="menu-style">
                                <img src={brgr.img} alt="burger food" height="80px" style={{}}/>
                                <h1>{brgr.title}</h1>
                                <p>{brgr.desc}</p>
                                <h4>${brgr.price}</h4>
                            </div>
                          
                            
                        </Card>
                    </Grid>
                    
                    )
                }   

           </Grid>
           </Container>
        </div>
    );
};

export default Burger;