import React, { useEffect, useState } from 'react';
import { BrowserRouter,Switch,Route,NavLink } from 'react-router-dom';
import Burger from '../Burger/Burger';
import Drinks from '../Drinks/Drinks';
import Snaks from '../Snaks/Snaks';
import './Menu.css'

const Menu = () => {
    return (
        <div>
            <div className="menu-header" style={{}}>
                <h1>OUR BEST MENU</h1>
                <hr/>
                <p>Lorem ipsum dolor sit amet, consectetuer adipiscing eLinkt. Aenean commodo Linkgula eget dolor. Aenean massa.</p>

            </div>
  
           

            <BrowserRouter>

            
       
            <div className="nav-menu">
                <ul>
                    <NavLink className="active"
                     to ="/snaks"
                     activeStyle={{
                        fontWeight: "bold",
                        color:" #000",
                        background: "#FFBA00",
                    }}
                     >Snakes</NavLink>
                    <NavLink to ="/burger"
                            activeStyle={{
                                fontWeight: "bold",
                                color:" #000",
                                background: "#FFBA00",
                            }}
                    >Burger</NavLink>
                    <NavLink to ="/drinks"

                             activeStyle={{
                                fontWeight: "bold",
                                color:" #000",
                                background: "#FFBA00",
                            }}>Drinks</NavLink>

                </ul>
            </div>
          

                <Switch>

                
                    <Route path="/snaks">
                       <Snaks></Snaks> 
                    </Route>

                    <Route path="/burger">
                      <Burger></Burger>
                    </Route>

                    <Route path="/drinks">
                       <Drinks></Drinks>
                    </Route>


                </Switch>
            </BrowserRouter>

        </div>
    );
};

export default Menu;