import { Card, CardContent, CardMedia, Container, Grid, Typography } from '@mui/material';
import React, { useEffect, useState } from 'react';
import './Snaks.css'

const Snaks = () => {
    const[snaks,setSnaks]=useState([])
    useEffect(()=>{
        fetch('snaks.json')
        .then(res=>res.json())
        .then(data=>setSnaks(data))
    },[])

    return (
        <div>
   
        <Container>
            <Grid container spacing={2}>
                {
                    snaks.map(snks=>

                    <Grid item xs={6} md={4}>
                        <Card sx={{ maxWidth: 600,backgroundColor:'black'}}>
                            <div className="menu-style">
                                <img src={snks.img} alt="snaks food" height="80px" style={{}}/>
                                <h1>{snks.title}</h1>
                                <p>{snks.desc}</p>
                                <h4>${snks.price}</h4>
                            </div>
                          
                            
                        </Card>
                    </Grid>
                    
                    )
                }   

           </Grid>
           </Container>
        </div>
    );
};

export default Snaks;