import React, { useEffect, useState } from 'react';
import './Drinks.css'
import { Card,Container,Grid } from '@mui/material';
const Drinks = () => {

   const[drinks,setDrinks]=useState([])
    useEffect(()=>{
        fetch('drinks.json')
        .then(res=>res.json())
        .then(data=>setDrinks(data))
    },[])

    return (
        <div>
            
            <Container>
            <Grid container spacing={2}>
                {
                    drinks.map(dnks=>

                    <Grid item xs={6} md={4}>
                        <Card sx={{ maxWidth: 600,backgroundColor:'black'}}>
                            <div className="menu-style">
                                <img src={dnks.img} alt="drinks food" height="80px" style={{}}/>
                                <h1>{dnks.title}</h1>
                                <p>{dnks.desc}</p>
                                <h4>${dnks.price}</h4>
                            </div>
                          
                            
                        </Card>
                    </Grid>
                    
                    )
                }   

           </Grid>
           </Container>
        </div>
    );
};

export default Drinks;